. Having a busi-ness account also makes it easier for diners to contact you through Instagram.")]),_c('p',[_vm._v("When you create your account, also take a moment to create a location page for your restaurant, which will allow other users to easily tag you in photos they post when they visit, as well as curate posts made at your restaurant onto one page.")]),_c('p',[_vm._v("If you use other forms of social media, like Facebook and Twitter, integrate your Instagram with those accounts. Al-ways make sure your Instagram photos are posting to your other social profiles, and cross promote your new Instagram account there to increase reach and follower counts. And consider embedding a stream of your most recent Instagram photos on your website, too.")]),_vm._m(1),_c('a',{attrs:{"href":require('../assets/images/chapter__11_image_2.jpg'),"target":"_blank","data-fancybox":"gallery"}},[_c('img',{attrs:{"src":require('../assets/images/chapter__11_image_2.jpg'),"alt":""}})])]),_vm._m(2),_c('section',[_c('h2',[_vm._v("When should you post?")]),_c('p',[_vm._v("Ideally, you should be posting to your Instagram daily, or even multiple times per day. Try out regular, daily posts, like posting photos and descriptions of your specials.")]),_c('p',[_vm._v("But you should also definitely take advantage of Instagram when special events are happening.")]),_c('div',{staticClass:"md-layout"},[_vm._m(3),_c('div',{staticClass:"md-layout-item md-small-size-100"},[_c('a',{attrs:{"href":require('../assets/images/chapter__11_image_3.png'),"target":"_blank","data-fancybox":"gallery"}},[_c('img',{attrs:{"src":require('../assets/images/chapter__11_image_3.png'),"alt":""}})])])]),_c('p',[_vm._v("You can even use Instagram to host a contest where you allow a guest to name a new food or drink item.")])]),_c('section',[_c('h2',[_vm._v("What should you post?")]),_c('p',[_vm._v("While everything in this guide will help you create the best In-stagram account possible, nothing matters as much as what you post. Your content is what will gain you followers, and turn those followers into guests who show up for your food.")]),_c('div',{staticClass:"md-layout"},[_c('div',{staticClass:"md-layout-item md-small-size-100"},[_c('a',{attrs:{"href":require('../assets/images/chapter__11_image_4.jpg'),"target":"_blank","data-fancybox":"gallery"}},[_c('img',{attrs:{"src":require('../assets/images/chapter__11_image_4.jpg'),"alt":""}})])]),_vm._m(4)]),_c('div',{staticClass:"md-layout"},[_vm._m(5),_c('div',{staticClass:"md-layout-item md-small-size-100"},[_c('a',{attrs:{"href":require('../assets/images/chapter__11_image_5.jpg'),"target":"_blank","data-fancybox":"gallery"}},[_c('img',{attrs:{"src":require('../assets/images/chapter__11_image_5.jpg'),"alt":""}})])])]),_c('p',[_vm._v("A few tips for getting the best possible food photography, even as a novice:")]),_vm._m(6),_c('p',[_vm._v("But food shouldnt be all you post. Instagram is also a great way to show the behind-the-scenes workings of your restaurant. Show off your ambiance. Introduce your staff. Let your followers follow along with a renovation project. Instagram can make your restaurant and its staff seem a little more human, so let it do just that.")]),_c('p',[_vm._v("While Instagram has always been a photo sharing platform, its introduced new ways to create content with video and stories. Take advantage of these! A video of a glass of wine being poured is a lot more interesting  and mouth-water-ing  than a still shot. A video tour of your kitchen can allow followers to meet your chef and see them in their element.")]),_c('p',[_vm._v("When it comes to daily content, dont feel like you have to create it all yourself. Embrace the art of regramming  post-ing customers photos that they took at your restaurant. Your hashtags and location tags will really help with this. One note: Be sure to tag the original poster. It will let them know that you posted their content and it builds a way for you to con-nect with a customer--making the relationship even stronger.")])]),_c('section',[_c('h2',[_vm._v("Instagram is not a one-way street")]),_c('p',[_vm._v("Beware: Instagram isnt a visual press conference for your brand.")]),_c('p',[_vm._v("You should constantly be interacting, liking, commenting, and engaging with your followers. Take the time to dig into their relevant content that relates to your brand and engage with it.")]),_c('p',[_vm._v("Aside from posting, use Instagram to have positive interac-tions with your fans. Post captions that inspire them to com-ment (a fill in the blank is a great option, like The best burger topping is ___), then start a dialogue. Answer questions. Encourage opinions. Engage respectfully, but with a fun tone.")]),_c('p',[_vm._v("Armed with the right tips and tricks, its easy to see that Ins-tagram is a powerful marketing tool  and that it isnt nearly as scary as it might seem to a less-than-tech-savvy restaura-teur. Use this guide to create a restaurant Instagram account that will not only show off the great work youre doing, but will inspire diners to become followers.")]),_c('a',{attrs:{"href":require('../assets/images/chapter__11_image_6.jpg'),"target":"_blank","data-fancybox":"gallery"}},[_c('img',{staticClass:"mb-0",attrs:{"src":require('../assets/images/chapter__11_image_6.jpg'),"alt":""}})])])])}
