livering quality customer service also means being prompt, and that is multifaceted. From the minute a custom-er walks through your door, its time to start giving them that excellent experience. Greet them immediately when they enter your restaurant, but be authentic. No one likes walking into a restaurant feeling like the greeting they just received was scripted. Listen to customers intently. And dont make them wait. Try to get the first round of drinks or appetizers to the table as quickly as possible  drinks within 10 minutes of their arrival and appetizers within 15-20 minutes of their arrival ideally"),_c('sup',[_c('a',{attrs:{"href":"https://www.quora.com/What-is-the-average-waiting-time-for-a-meal-to-be-served-after-the-customer-has-ordered-in-a-restaurant","target":"_blank"}},[_vm._v("[20]")])]),_vm._v(". Promptness can be the difference between a happy guest who becomes a repeat customer, and a hangry one who never comes back.")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',[_vm._v("Another great way to deliver consistently excellent experiences is to personalize your customer service to each guest"),_c('sup',[_c('a',{attrs:{"href":"https://www.theglobeandmail.com/life/food-and-wine/food-trends/a-cycle-of-exploitation-how-restaurants-get-cooks-to-work-12-hour-days-for-minimum-wage-or-less/article26999168/","target":"_blank"}},[_vm._v("[21]")])]),_vm._v(". Maintain a guest database and use it to offer special promotions to loyal customers. Encourage servers to get to know regulars and address them by name. Train your staff to know the menu well enough to offer suggestions based on what customers like or what theyve already ordered. Recommending the perfect drink or dessert to compliment a meal will make a customer feel especially taken care of.")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',[_vm._v("You may be reading this and thinking that providing excellent customer service takes a lot of work. It does. But, its well worth the time and effort you invest. Happy customers are repeat customers, and theres data to prove it: A Harvard Business School study on Starbucks customers found that satisfied customers visited, on average, 4.3 times per month, spending $4.06 per visit and remaining customers for 4.4 years"),_c('sup',[_c('a',{attrs:{"href":"https://www.webstaurantstore.com/article/119/how-to-hire-a-chef.html","target":"_blank"}},[_vm._v("[22]")])]),_vm._v(".")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',[_vm._v("o what about the flip side? A Customer Experience Report survey found that rude customer service was one of the top reasons customers will abandon a brand, beating out other reasons by 18 percent "),_c('sup',[_c('a',{attrs:{"href":"https://www.slideshare.net/RightNow/2010-customer-experience-impact","target":"_blank"}},[_vm._v("[23]")])]),_vm._v(".")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',[_c('h3',[_vm._v("How Restaurants Need to Take Customer Service Further")]),_c('p',[_vm._v("Of course, customer service varies a bit across industries.")]),_c('p',[_vm._v("For restaurants, specifically, theres food consistency to worry about. Think about your restaurants signature dish. Its fresh. Its flavorful. Customers come back over and over because they crave it. Now think about how that would all change if the dish werent consistent every time a customer ordered it. Imagine if a customer came back, specifically for that dish, and got something other than what they expect-ed, what theyd had before. Thats why food consistency is vital to your customer service strategy and, ultimately, your success.")]),_c('p',[_vm._v("So how do you ensure the kind of consistency that will have customers coming back over and over for their favorite dish-es? There are a lot of strategies you can employ"),_c('sup',[_c('a',{attrs:{"href":"